.k-chart,
.k-sparkline,
.k-stockchart {
  border-color: #dee2e6;
  color: #5c6466;
  background-color: transparent;
}

.k-chart .k-popup,
.k-sparkline .k-popup,
.k-stockchart .k-popup {
  background: transparent;
}

.k-chart-tooltip {
  color: #fff;
}

.k-chart-tooltip-inverse {
  color: black;
}

.k-chart-crosshair-tooltip,
.k-chart-shared-tooltip {
  color: #000;
  background-color: #f7f7f7;
  border-color: rgba(0, 0, 0, 0.08);
}

.k-selection {
  border-color: rgba(0, 0, 0, 0.08);
  box-shadow: inset 0 1px 7px rgba(0, 0, 0, 0.15);
}

.k-selection-bg {
  background-color: transparent;
}

.k-handle {
  cursor: e-resize;
  border-color: #597683;
  color: #ffffff;
  background-color: #597683;
}

.k-handle:hover {
  border-color: #445b65;
  color: #ffffff;
  background-color: #4a616c;
}

.k-handle div {
  background-color: transparent;
}

.k-mask {
  background-color: #fff;
  opacity: .8;
}

.k-marquee-color {
  background-color: #263d4b;
}

.k-marquee-color {
  opacity: .6;
}

.k-treemap {
  border-color: #dee2e6;
  color: #5c6466;
  background-color: #fff;
}

.k-treemap .k-treemap-title {
  border-color: #dee2e6;
  color: #5c6466;
  background-color: #f8f9fa;
}

.k-treemap .k-leaf {
  color: #fff;
}

.k-treemap .k-leaf.k-inverse {
  color: #5c6466;
}

.k-treemap .k-leaf.k-state-hover {
  box-shadow: inset 0 0 0 3px #dee2e6;
}

.k-map {
  height: 600px;
  border-width: 0px;
  border-style: solid;
  font-size: 1rem;
  line-height: 1.5;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}

.k-map .km-scroll-wrapper {
  width: 100%;
  height: 100%;
  user-select: none;
  position: absolute;
}

.k-map .km-scroll-container {
  height: 100%;
}

.k-map .k-touch-scrollbar {
  display: none;
}

.k-map .k-layer {
  position: absolute;
  left: 0;
  top: 0;
}

.k-map .k-marker {
  transform: translate(-50%, -100%);
  font-size: 32px;
  cursor: pointer;
  position: absolute;
  overflow: visible;
}

.k-map .k-attribution {
  padding: 0.25rem 0.5rem;
  border-width: 0;
  font-size: 0.75rem;
  z-index: 1000;
}

.k-map-controls {
  position: absolute;
  display: flex;
  align-items: center;
}

.k-navigator {
  margin: 1rem;
  width: calc( 48px + 4px);
  height: calc( 48px + 4px);
  border-width: 1px;
  border-style: solid;
  box-sizing: border-box;
  border-radius: 50%;
  position: relative;
}

.k-pdf-export .k-navigator {
  display: none;
}

.k-navigator .k-button {
  margin: 0;
  padding: 0;
  border-radius: 100%;
  line-height: 1;
  box-shadow: none;
  position: absolute;
}

.k-navigator .k-button:not(:hover) {
  border-color: transparent;
  background: none;
}

.k-navigator .k-navigator-up {
  transform: translateX(-50%);
  top: 2px;
  left: 50%;
}

.k-navigator .k-navigator-right {
  transform: translateY(-50%);
  right: 2px;
  top: 50%;
}

.k-navigator .k-navigator-down {
  transform: translateX(-50%);
  bottom: 2px;
  left: 50%;
}

.k-navigator .k-navigator-left {
  transform: translateY(-50%);
  left: 2px;
  top: 50%;
}

.k-zoom-control {
  margin: 1rem;
  border: 0;
  background: none;
  display: flex;
}

.k-pdf-export .k-zoom-control {
  display: none;
}

.k-zoom-control .k-button {
  padding: 0.375rem;
}

.k-map {
  border-color: #dee2e6;
  color: #5c6466;
  background-color: #fff;
}

.k-map .k-navigator {
  border-color: #597683;
  color: #ffffff;
  background-color: #597683;
}

.k-map .k-marker {
  color: #263d4b;
}

.k-map .k-attribution {
  background-color: rgba(255, 255, 255, 0.8);
}

html {
  font-family: sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #000;
  text-align: left;
  background-color: #fff;
}
